import React from 'react';
import '../app.css'
class Searchbar extends React.Component{
    state={text:''}

    onipchange=(event)=>{
        this.setState({text:event.target.value});

    }

    inputchange=(event)=>
    {
        event.preventDefault();
       // console.log(event.target[0].value);
       this.props.onrun(this.state.text);    //{/* This is how you use props in a className based component this.props. */}   

       
    }

    render()
    {
        return(
            <div>
                
                <div className="marg align center">

                    <form onSubmit={this.inputchange}>
                        
                            <input onChange={this.onipchange} className="ui input focus big" type="text" />
                            <button className="positive ui button forbutton" type="submit">Search</button>
                            <br />
                            
                    </form>
                    
                </div>
            </div>
        );
    }




}

export default Searchbar;