import React from 'react';
import Searchbar from './Searchbar';
//import axios from "axios";
import unsplash from "../apis/unsplash"
import Imagelist from "./Imagelist";
import applogo from "../images/imagle.png"
import Footercomp from "./footercomp"

class App extends React.Component
{
    //state
    state={
        images:[]
    }

    

    //this is going to do my request
     onrun= async (searchterm)=>{
       const response= await 
        unsplash.get("/search/photos",
        {
            params:
            {
                query:searchterm
            }
            
        });
        //console.log(this)
        this.setState({images: response.data.results });
        //console.log(response.data.results)
        // Using a promise
        // .then(response=>{
        //     console.log(response.data.results)
        // }).catch(err=>console.log(err))
        
    }

    render()
    {
        return (<div>
            <div className="grid-container ">

                <div className="grid-item lilmarg">
                    <img src={applogo} alt={"logo"} height={100} width={350}></img>
                </div>

                <div className="grid-item">
                <Searchbar onrun={this.onrun}/>
                </div>

            </div>
            <div>

                
            <div className="ui segment">
                    <h2 className="ui left floated header">Search Results</h2>
                    <Imagelist images={this.state.images} />

            </div>


            <div>
                <Footercomp />
            </div>
                



            </div>
            <br/>
            
            {/* now what we have to do is take state data from searchbar component so */}
        </div>);
    }
}


export default App;